.productItem {
    max-width: 500px;

    .slider {
        position: relative;

        .swiper_block {
            max-width: 500px;
            max-height: 340px;

            .swiper-pagination {
                bottom: 0px !important;
            }
        }

        .advertised {
            font-size: 18px;
            font-weight: bold;
            line-height: 24px;
            color: #665CD1;
            background-color: #F1F0FF;
            padding: 5px 15px;
            position: absolute;
            left: 10px;
            top: 10px;
            z-index: 10000;
            border-radius: 8px;
        }

        .verificated {
            position: absolute;
            z-index: 10000;
            top: -10px;
            right: -10px;
        }
    }



    .info_block {
        display: flex;
        justify-content: space-between;
        padding-top: 10px;

        .title_price_block {
            font-size: 26px;
            font-weight: bold;
            line-height: 45px;
            margin: 0px;

            p {
                margin: 0px;
            }

            .price {
                color: #665CD1;
            }
        }

        .mark_square_block {

            .mark_block {
                display: flex;
                justify-content: flex-end;
                align-items: center;
                text-align: right;
                font-size: 20px;
                line-height: 30px;
                color: #5A5A5A;

                p {
                    margin: 0px;
                    font-weight: bold;
                }

            }

            .square_block {
                font-size: 20px;
                font-weight: 400;
                line-height: 30px;
                color: #5A5A5A;
            }

        }
    }
}

@media (max-width: 570px) {

    .productItem {
        max-width: 340px;
        margin: 0 auto;

        .slider {
            .swiper_block {
                max-width: 340px;
                max-height: 340px;
            }

            .advertised {
                font-size: 16px;
                line-height: 20px;
            }
        }

        .info_block {

            .title_price_block {
                font-size: 18px;
                line-height: 30px;
            }

            .mark_square_block {

                .mark_block {
                    font-size: 18px;
                }

                .square_block {
                    font-size: 18px;
                }
            }
        }
    }
}

@media (max-width:768px) {

    .productItem {
        margin: 0 auto;
    }
}

@media (max-width:1200px) {
    .productItem {
        margin: 0 auto;
    }
}