.form {
    box-shadow: 0px 9px 40px 0px rgba(0, 0, 0, 0.15);
    border-radius: 40px;

    input {
        font-size: 24px;
        width: 373px;
        border: none;
        border-bottom: 2px solid #665CD1;

    }

    input:focus {
        border-bottom: 2px solid #FF0000;
    }


    &_container {
        padding: 30px 50px 0px 50px;

    }

    &_title {
        font-size: 48px;
        line-height: 60px;
        font-weight: bold;
        color: #171717;
        padding: 10px 0px 40px 0px;
        text-align: center;
        margin: 0;
    }

    &_password {
        display: flex;
        justify-content: space-between;

        &_input {
            display: flex;
            max-width: 373px;
        }
    }

    &_wrapper {
        display: flex;
        flex-direction: column;
        gap: 5px;

        .form_input {
            display: flex;
            justify-content: space-between;
        }

        label {
            align-self: start;
            font-size: 24px;
            line-height: 30px;
            margin: 0px;
            padding-right: 20px;
            padding-top: 5px;
        }
    }


    button {
        margin-top: 35px;
        margin-bottom: 30px;
    }
}

.error_container {
    height: 21px;
}

.error {
    font-size: 14px;
    color: #FF0000;
}



@media (max-width: 570px) {
    .form {
        box-shadow: none;
        border-radius: none;

        input {
            font-size: 20px;
            font-weight: 500;
            width: 300px;
        }

        &_container {
            padding: 0px;
            padding-top: 30px;
        }

        &_title {
            font-size: 38px;
            line-height: 48px;
            padding: 10px 0px 30px 0px;

        }

        &_password {

            &_input {
                display: flex;
                max-width: 300px;
            }
        }

        &_wrapper {
            gap: 0px;

            .form_input {
                display: flex;
                flex-direction: column;
            }

            label {
                font-size: 14px;
                line-height: 18px;
                padding: 0px;
            }
        }


        button {
            margin-top: 0px;
        }
    }

    .error_container {
        height: 16px;
        margin-bottom: 0px;
    }

    .error {
        font-size: 10px;
    }
}

@media (max-width:768px) {
    .form {
        border-radius: none;
        box-shadow: none;
    }
}