.checkout {
    padding-top: 100px;
    text-align: left;
    margin: 0 auto;

    h2 {
        padding-bottom: 50px;
        font-size: 36px;
        line-height: 45px;
        font-weight: bold;
    }

    &_content {
        display: flex;

        .checkout_product {
            h3 {
                font-size: 24px;
                font-weight: bold;
                line-height: 30px;
                padding-bottom: 25px;
                margin: 0px;
            }
        }

        .chekout_form {
            .form {

                &_container {

                    .form_wrapper {
                        display: flex;
                        gap: 100px;
                        padding-left: 100px;

                        .form_input_wrapper {
                            text-align: left;

                            h3 {
                                font-size: 24px;
                                font-weight: bold;
                                line-height: 30px;
                            }

                            .form_input {
                                label {
                                    padding-top: 15px;
                                    font-size: 16px;
                                    line-height: 20px;
                                    margin: 0px;

                                }

                                input {
                                    font-size: 24px;
                                    width: 343px;
                                    border: none;
                                    border-bottom: 2px solid #665CD1;
                                }

                                input:focus {
                                    border-bottom: 2px solid #FF0000;
                                }

                            }
                        }

                        .form_radio_wrapper {
                            display: flex;
                            flex-direction: column;
                            gap: 25px;

                            button {
                                margin-top: 100px;
                            }

                            h3 {
                                text-align: left;
                                font-size: 24px;
                                font-weight: bold;
                                line-height: 30px;

                            }

                            .form_radio {
                                background-color: #F1F0FF;
                                text-align: left;
                                vertical-align: middle;
                                border-radius: 5px;




                                label {
                                    vertical-align: middle;
                                    font-size: 19px;
                                    font-weight: bold;
                                    line-height: 24px;
                                    padding: 13px 150px 13px 0px;
                                    margin: 0px;
                                }

                                input {
                                    vertical-align: middle;
                                    width: 22px;
                                    height: 22px;
                                    margin: 0px 24px 0px 19px;

                                }

                                input:checked {
                                    color: #665CD1;
                                }


                            }
                        }
                    }

                }
            }
        }
    }


}

.error_container {
    height: 21px;
}

.error {
    font-size: 14px;
    color: #FF0000;
}



