.registration_text {
    text-align: center;
    font-size: 36px;
    line-height: 45px;
    padding-bottom: 30px;
}

.registration_btn {
    display: flex;
    justify-content: center;
    gap: 80px;
}

.registration_info {
    text-align: center;
    padding-top: 40px;

    p {
        margin: 0px;
    }

    a {
        color: #665CD1;
        font-weight: bold;
        line-height: 20px;
        text-decoration: 1px underline;
        cursor: pointer;
    }
}

@media (max-width: 570px) {
    .registration_text {
        font-size: 32px;
        line-height: 40px;
        padding-bottom: 25px;
        margin: 0px;
    }

    .registration_info {
        font-size: 14px;
        line-height: 18px;
    }

}

@media (max-width:768px) {

    .registration_btn {
        gap: 50px;
    }
}