.formMessage {
    color: #171717;
    font-size: 48px;
    font-weight: bold;
    text-align: center;

    &_text {
        padding-bottom: 70px;
    }
}

@media (max-width: 570px) {
    .formMessage {
        font-size: 38px;

        &_text {
            padding-bottom: 40px;
            margin: 0px;
        }
    }
}