.verification {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 100px;

}

.verification_wrapper {
    padding: 30px 50px;
    box-shadow: 0px 9px 40px 0px rgba(0, 0, 0, 0.15);
    border-radius: 40px;
}

.verification_title {
    font-style: 36px;
    line-height: 45px;
    font-weight: bold;
    text-align: center;
}

.verification_text {
    font-style: 19px;
    line-height: 24px;
    padding: 0px 30px;
    text-align: center;
    margin: 0px;
}

.qrCode_box {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
}

.timer {
    text-align: center;
    color: #665CD1;
    font-weight: 400;
    padding: 0px 0px;
    min-width: 325px;

}

.verification_btn {
    text-align: center;

    button {
        border-radius: 30px;
        border: none;

        span {
            padding: 10px 15px;
            font-size: 19px;
            display: inline-block;
            vertical-align: center;
        }
    }

    .activeButton {
        color: #EBEBEB;
        background-color: #171717;
        cursor: pointer;
        box-shadow: 6px 6px 30px 0px rgba(102, 92, 209, 0.4);
    }

    .disabledButton {
        background-color: #cccccc;
        color: #666666;
        cursor: not-allowed;
    }
}

.verification_dia {
    margin: 30px 0px 70px 0px;

}

@media (max-width: 570px) {
    .verification {
        padding: 70px 15px 0px 15px;
    }

    .verification_wrapper {
        box-shadow: none;
        border-radius: 0px;
    }

    .timer {
        font-size: 14px;

    }
}