.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #FEFEFE;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 15px 0px 15px;
}

.logo {
    position: fixed;
    top: 29px;
    left: 45px;
    cursor: pointer;
}

@media (max-width: 570px) {
    .logo {
        top: 18px;
        left: 15px;
    }
}

@media (max-width:768px) {
    .logo {}
}