@import url('http://fonts.cdnfonts.com/css/mulish');

* {
    font-family: 'Mulish', sans-serif;
    outline: none !important;
}

html body #root {
    height: 100%;
}

.root_container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.container {
    height: 100%;
    flex: 1 1 auto;
    flex-grow: 1;

}

footer {
    margin-top: auto;
}

.container_ {
    min-height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}


a,
a:hover {
    text-decoration: none;
    color: #0E0E0E;
    cursor: pointer !important;
}

button {
    cursor: pointer;
}

.block-320 {
    display: none;
}

input {
    cursor: text !important;
}

label {
    cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}

.additional-menu-320 {
    display: none !important;
}

.row-none {
    display: none;
}

#menu__toggle {
    opacity: 0;
}

.menu__btn {
    align-items: center;
    position: absolute;
    top: 5px;
    right: 5px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: end;
    margin-bottom: 0px;
}

.menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: end;
    background-color: white;
    z-index: -1;

    li {

        img {
            padding-left: 22px;
        }

        display: flex;
        align-items: center;
        padding-top: 33px;
        padding-right: 50px;
        justify-content: end;
    }
}

.menu__item {
    display: block;
    color: rgba(90, 90, 90, 1) !important;
    font-size: 18px;
    font-weight: 400;
    text-decoration: none !important;
}

#menu__toggle:checked~.menu__btn>span {
    transform: rotate(45deg);
}

#menu__toggle:checked~.menu__btn>span::before {
    top: 0;
    transform: rotate(0);
}

#menu__toggle:checked~.menu__btn>span::after {
    top: 0;
    transform: rotate(90deg);
}

#menu__toggle:checked~.menu__box {
    visibility: visible;
    right: 0;
}

.menu__btn>span,
.menu__btn>span::before,
.menu__btn>span::after {
    display: block;
    position: absolute;
    width: 70%;
    height: 4px !important;
    display: flex;
    justify-content: end;
    background-color: #171717;
}

.menu__btn>span::before {
    content: '';
    top: -9px;
    width: 145%;
}

.menu__btn>span::after {
    content: '';
    top: 9px;
    width: 145%;
}

.menu__btn>span,
.menu__btn>span::before,
.menu__btn>span::after {
    transition-duration: .25s;
}

.menu__box {
    transition-duration: .25s;
}

.menu__item {
    transition-duration: .25s;
}

header {
    position: fixed;
    background: white;
    width: 100%;
    padding-top: 27px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 28px;
    border-bottom: 1px solid #E9E9E9;
    z-index: 100;
    flex: 0 0 auto;

    .logo {
        margin-right: 78px;
    }

    .menu-desktop {
        list-style-type: none;
        display: flex;
        margin: 0;
        padding: 0;

        li {
            margin-right: 53px;
            font-weight: 700;
            font-size: 18px;
            cursor: pointer;

            a {
                color: #5A5A5A;
                text-decoration: none;
            }
        }
    }

    .additional-menu {
        list-style-type: none;
        display: flex;
        margin: 0;

        li {
            margin-right: 32px;
            font-weight: 700;
            font-size: 18px;
            cursor: pointer;
        }
    }
}

.container-fluid {
    .row {
        justify-content: space-between;
    }
}


.load-page {
    .col-md-12 {
        padding: 27px 30px 30px 30px;
    }

    width: 100%;
    height: 100vh;

    .load-body {
        height: 80vh;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            animation: 1s linear 0s normal none infinite running rot;
            -webkit-animation: 1s linear 0s normal none infinite running rot;
            width: 100px !important;
            height: 100px !important;
        }

        @keyframes rot {
            0% {
                transform: rotate(0deg);
            }

            100% {
                transform: rotate(360deg);
            }
        }

        @-webkit-keyframes rot {
            0% {
                transform: rotate(0deg);
            }

            100% {
                transform: rotate(360deg);
            }
        }
    }
}

