.category_middle {
    display: flex;
    justify-content: space-between;
    margin: 15px 0px;
    cursor: pointer;

    .left {
        width: 80%;
        display: flex;
        flex-wrap: wrap;

        .span {
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            color: #665CD1;
            opacity: 0.5;
            margin-right: 10px;
        }
    }

    .right {
        display: flex;
        position: relative;

        .filter,
        .sort {

            .subCategoryBlock {
                .list-of-staps {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;

                    div {
                        display: flex;
                        align-items: center;
                    }

                    img {
                        margin: 0px 9px;
                    }

                    span {
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 100%;
                        color: #A4A4A4;
                        margin-right: 0px;
                    }
                }

                select {
                    background: none;
                    border: 2px solid #D9D9D9;
                    border-radius: 6px;
                    width: 100%;
                    height: 36px;
                    font-weight: 400;
                    font-size: 16px;
                    color: #5A5A5A;
                    margin-top: 10px;
                }
            }

            margin-left: 25px;

            span {
                font-weight: 400;
                font-size: 14px;
                line-height: 110%;
                text-align: right;
                color: #5A5A5A;
                margin-right: 5px;
            }

            .sort-block {
                width: 0px;
                height: 0px;
                padding: 0px;
                background: #FFF;
                border: 2px solid #EBEBEB;
                border-radius: 10px;
                position: absolute;
                z-index: 1;
                right: 0px;
                top: 30px;
                overflow: hidden;
                transition: .5s;
                display: flex;
                flex-direction: column;

                span {
                    width: 100%;
                    height: 1px;
                    background: #D9D9D9;
                    border-radius: 20px;
                }

                button {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 18px;
                    color: #5A5A5A;
                    background: none;
                    border: none;
                    display: flex;
                    align-items: center;
                    padding: 5px;
                }
            }

            .filter-block {
                width: 0px;
                height: 0px;
                padding: 0px;
                background: #F5F5F5;
                border: 2px solid #FFFFFF;
                border-radius: 10px;
                position: absolute;
                z-index: 1;
                right: 0px;
                top: 0px;
                overflow: hidden;
                transition: .5s;

                .head {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-bottom: 20px;

                    .close {
                        font-size: 16px;
                        color: #5A5A5A;
                    }
                }

                .footer {
                    display: flex;
                    justify-content: space-between;
                    margin-top: 25px;

                    .button1 {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 20px;
                        text-align: center;
                        color: #FFFFFF;
                        background: #171717;
                        box-shadow: 6px 6px 30px rgba(102, 92, 209, 0.4);
                        border-radius: 34px;
                        padding: 8px 25px;
                        border: none;
                        cursor: pointer;
                    }

                    .button2 {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 20px;
                        text-align: center;
                        border: 2px solid #C8C8C8;
                        filter: drop-shadow(6px 6px 30px rgba(102, 92, 209, 0.4));
                        border-radius: 34px;
                        color: #0E0E0E;
                        padding: 8px 27px;
                        cursor: pointer;
                    }
                }

                .main {
                    max-height: 350px;
                    overflow: auto;
                    padding-right: 20px;

                    .main-block-1 {
                        position: relative;
                        height: 67px;
                        z-index: 3;

                        span {
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 18px;
                            color: #A4A4A4;
                        }

                        ////


                        /// 
                        .select {
                            border: 2px solid #D9D9D9;
                            border-radius: 6px;
                            position: absolute;
                            top: 27px;
                            width: 256px;
                            overflow: hidden;
                            white-space: break-spaces;

                            select {
                                font-weight: 400;
                                color: #5A5A5A;
                                padding: 0px 5px;
                                margin: 0px;
                                border-radius: 6px;
                                background-color: #F5F5F5;
                                width: 100%;
                                border: none;

                                option {
                                    background-color: #F5F5F5;
                                    outline: none;
                                    font-size: 12px;
                                    padding: 5px 0px;
                                    max-width: 250px;

                                    overflow-wrap: break-word;
                                    white-space: nowrap;
                                    overflow: hidden;


                                }


                            }

                        }


                        ul {
                            overflow: hidden;
                            height: 0px;
                            padding: 0px;
                            margin: 0px;
                            background-color: #F5F5F5;
                            border-radius: 6px;

                            li {
                                margin: 0px 10px;
                                padding: 10px 0px;
                                font-weight: 400;
                                font-size: 16px;
                                line-height: 100%;
                                color: #5A5A5A;
                                border-top: solid 1px #5A5A5A;
                            }
                        }
                    }

                    .main-block-2,
                    .main-block-5,
                    .main-block-6 {
                        margin-top: 25px;

                        input {
                            position: absolute;
                            top: 0px;
                            visibility: hidden;
                        }

                        label {
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 100%;
                            color: #5A5A5A;
                            display: flex;
                            align-items: center;
                            margin-bottom: 0px;
                        }

                        label::before {
                            content: '';
                            width: 15px;
                            height: 15px;
                            display: block;
                            border: 2px solid #D9D9D9;
                            border-radius: 5px;
                            margin-right: 8px;
                        }

                        input:checked~label::before {
                            content: '';
                            width: 15px;
                            height: 15px;
                            display: block;
                            background: #86E0AF;
                            border: #86E0AF 2px solid;
                            border-radius: 5px;
                            margin-right: 8px;
                            background-image: url(./assets/galka.svg);
                            background-repeat: no-repeat;
                            background-position-x: center;
                            background-position-y: center;
                        }
                    }

                    .main-block-3,
                    .main-block-4 {
                        margin-top: 16px;

                        span {
                            font-size: 14px;
                            line-height: 18px;
                            color: #A4A4A4;
                            margin-bottom: 10px;
                            display: block;
                            text-align: left;
                        }

                        div {
                            display: flex;
                            align-items: center;

                            input {
                                border: 2px solid #D9D9D9;
                                border-radius: 6px;
                                width: 100px;
                                padding: 10px;
                                font-weight: 400;
                                font-size: 16px;
                                line-height: 100%;
                                color: #5A5A5A;
                                height: 36px;
                                background: none;
                            }

                            span {
                                margin-right: 0px;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 100%;
                                color: #5A5A5A;
                                padding: 0px;
                                margin: 0px;
                                margin-left: 5px;
                                margin-right: 5px;
                            }

                            .kmm {
                                margin: 0px;
                                margin-left: 10px;
                            }
                        }
                    }
                }

                .main-block-7 {
                    display: none;
                }
            }
        }
    }
}

