.chat-main {
    display: flex;
    width: 100%;
    padding: 0px 50px;
}

.chatContainer {
    padding-top: 84px;
    height: 77vh;
    width: 100%;
    display: flex;

    .list-friends {
        width: 25%;
        padding: 6px 21px 6px 8px;
        border-right: 2px solid #D9D9D9;
        height: 100%;

        .item {
            padding: 10px;
            display: flex;
            height: 72px;
            position: relative;
            cursor: pointer;

            .logo {
                width: 52px;
                height: 52px;
                border-radius: 100%;
                background: #665CD1;
                font-weight: 700;
                font-size: 24px;
                line-height: 30px;
                text-align: center;
                color: #FFFFFF;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 10px;
                min-width: 52px;
                background-size: cover;
                background-repeat: no-repeat;
            }

            .body {
                display: flex;
                flex-direction: column;
                transition: 1s;

                .name {
                    font-weight: 400;
                    font-size: 19px;
                    line-height: 24px;
                    color: #5A5A5A;
                    margin-bottom: 0px;
                }

                .mess {
                    font-weight: 400;
                    font-size: 11px;
                    line-height: 14px;
                    color: #A4A4A4;
                    margin-bottom: 0px;
                }

                .time {
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 120%;
                    text-align: right;
                    color: #A4A4A4;
                    opacity: 0.7;
                    margin-bottom: 0px;
                    position: absolute;
                    top: 16px;
                    right: 10px;
                }

                .newMess {
                    position: absolute;
                    right: 10px;
                    bottom: 10px;
                    background: #665CD1;
                    width: 23px;
                    height: 23px;
                    color: white;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 100%;
                }
            }
        }
    }

    .chatBody {
        width: 75%;
        padding-top: 20px;

        .chatImages {
            width: auto;
            height: auto;
            max-width: 250px;
            max-height: 400px;
            min-height: 100px;
            min-width: 100px;
            border-radius: 10px;
            margin-top: 10px;
            margin-bottom: 10px;
        }

        .timeOfMessPuc {
            font-weight: 300;
            font-size: 19px;
            line-height: 24px;
            text-align: center;
            color: #5A5A5A;
            display: block;
            margin: 20px auto;
            padding: 5px 15px;
            border-radius: 20px;
            border: solid 1px #5A5A5A;
            width: min-content;
        }

        .chatContent {
            height: 90%;
            overflow: auto;

            .message {
                display: flex;
                align-items: end;
                margin-bottom: 5px;
                position: relative;
                width: max-content;
                margin-left: 30px;

                .logo {
                    background: #665CD1;
                    width: 30px;
                    height: 30px;
                    color: white;
                    border-radius: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-right: 15px;
                    background-size: cover;
                    background-repeat: no-repeat;
                }

                .body {
                    padding: 10px 30px 30px 10px;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 120%;
                    color: #5A5A5A;
                    border: 1px solid #7E7E7E;
                    border-radius: 10px 10px 10px 0px;
                    max-width: 300px;
                    overflow-wrap: break-word;
                }

                .time {
                    position: absolute;
                    bottom: 7px;
                    right: 7px;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 120%;
                    text-align: right;
                    color: #5A5A5A;
                    opacity: 0.5;
                }

                .readingStatus {
                    display: none;
                }
            }

            .myMessage {
                flex-direction: row-reverse;
                margin-left: auto;
                margin-right: 30px;
                max-width: 350px;

                .logo {
                    margin-right: 0px;
                    margin-left: 15px;
                }

                .body {
                    border-radius: 10px 10px 0px 10px;
                    border: 1px solid #665CD1;
                    background: #F1F0FF;
                    padding: 10px 30px 30px 10px;
                }

                .time {
                    bottom: 7px;
                    right: 50px;
                }

                .readingStatus {
                    position: absolute;
                    right: auto;
                    bottom: 5px;
                    left: 10px;
                    display: block;
                }
            }
        }
    }

    .writing {
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        color: #5A5A5A;
        margin-left: 30px;
        margin-top: 10px;
    }

    .send_panel {
        display: flex;
        background: #FFFFFF;
        align-items: center;
        padding: 0px 30px;
        padding-top: 10px;

        input {
            border: 2px solid #D9D9D9;
            border-radius: 6px;
            height: 38px;
            width: 100%;
        }

        .inputImg {
            border: 2px solid #D9D9D9;
            border-radius: 6px;
            height: auto;
            width: 100%;
            display: flex;
            overflow: auto;

            span {
                margin-right: 20px;
                border-radius: 8px;
                min-width: 200px;
                min-height: 100px;
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center center;
                width: 177px;
                height: 97px;
                display: block;
                margin: 10px;
            }
        }

        .send-mess-button {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            text-align: left;
            color: #FEFEFE;
            background: #F1F0FF;
            box-shadow: 11px 11px 17px rgba(193, 193, 193, 0.14);
            border-radius: 35px;
            background-image: url(./assets/send.svg);
            background-repeat: no-repeat;
            background-position-x: 107px;
            background-position-y: center;
            background-size: 25px;
            margin-left: 18px;
            cursor: pointer;
            color: #665CD1;
            border: none;
            width: 150px;
            min-width: 150px;
            height: 50px;
            padding-left: 15px;
        }

        .fileLabel {
            width: 25px;
            height: 30px;
            min-width: 25px;
            min-height: 30px;
            margin-top: auto;
            margin-bottom: auto;
            margin-left: 17px;
            background-image: url(./assets/zakolka.svg);
        }

        .fileInput {
            visibility: hidden;
        }
    }
}

