    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #665CD1;
    color: #665CD1;
    box-shadow: 9984px 0 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    animation: dotTyping 1.5s infinite linear;
}

@keyframes dotTyping {
    0% {
        box-shadow: 9984px 0 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    }

    16.667% {
        box-shadow: 9984px -10px 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    }

    33.333% {
        box-shadow: 9984px 0 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    }

    50% {
        box-shadow: 9984px 0 0 0 #665CD1, 9999px -10px 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    }

    66.667% {
        box-shadow: 9984px 0 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    }

    83.333% {
        box-shadow: 9984px 0 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px -10px 0 0 #665CD1;
    }

    100% {
        box-shadow: 9984px 0 0 0 #665CD1, 9999px 0 0 0 #665CD1, 10014px 0 0 0 #665CD1;
    }
}

