.scrollToTop {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    animation: fadeIn 700ms ease-in-out 1s both;
    cursor: pointer;
    color: #171717;
    z-index: 1000000;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@media screen and (max-width: 570px) {
    .scrollToTop {
        color: #171717b3;
    }
}