.form {
    box-shadow: 0px 9px 40px 0px rgba(0, 0, 0, 0.15);
    border-radius: 40px;

    input {
        font-size: 24px;
        width: 373px;
        border: none;
        border-bottom: 2px solid #665CD1;

    }

    input:focus {
        border-bottom: 2px solid #FF0000;
    }

    &_container {
        padding: 0px 50px 30px 50px
    }

    &_title {
        font-size: 48px;
        line-height: 60px;
        font-weight: bold;
        color: #171717;
        padding: 30px 0px 60px 0px;
        text-align: center;
        margin: 0px;
    }

    &_password {
        display: flex;
        justify-content: space-between;

        &_input {
            display: flex;
            max-width: 373px;

            svg {
                align-self: center;
                cursor: pointer;
            }
        }
    }


    &_password_recovery {
        color: #665CD1;
        cursor: pointer;
    }

    &_info {
        padding-top: 30px;
        padding-bottom: 30px;
        text-align: center;

        p {
            padding: 0px;
            margin: 0px;
        }

        &_registration_link {
            color: #665CD1;
            font-weight: bold;
            line-height: 20px;
            text-decoration: 1px underline;
            cursor: pointer;
        }
    }

    &_wrapper {
        display: flex;
        flex-direction: column;
        gap: 5px;

        .form_input {
            display: flex;
            justify-content: space-between;
        }

        label {

            font-size: 24px;
            line-height: 30px;
            margin: 0px;
            justify-self: center;
            padding-right: 20px;
        }
    }
}


.error_container {
    height: 21px;
    margin-bottom: 10px;
}

.error {
    font-size: 14px;
    color: #FF0000;
}

.registration_text {
    text-align: center;
    font-size: 36px;
    line-height: 45px;
    padding-bottom: 30px;
}




@media (max-width: 570px) {
    .form {
        border-radius: none;
        box-shadow: none;

        input {
            font-size: 20px;
            font-weight: 500;
            width: 300px;
        }

        &_container {
            padding: 0px 50px 0px 50px
        }

        &_title {
            font-size: 38px;
            line-height: 48px;
        }

        &_password {

            &_input {
                display: flex;
                max-width: 300px;

            }
        }

        &_password_recovery {
            font-size: 14px;
            line-height: 18px;
        }

        &_info {
            font-size: 14px;
            line-height: 18px;

            &_registration_link {
                font-size: 14px;
                line-height: 18px;
            }
        }

        &_wrapper {
            .form_input {
                display: flex;
                flex-direction: column;
            }

            label {
                font-size: 14px;
                line-height: 18px;
                padding-right: 0px;
            }
        }
    }

    .error_container {
        height: 16px;
        margin-bottom: 0px;
    }

    .error {
        font-size: 10px;
    }

    .registration_text {
        font-size: 24px;
        line-height: 32px;
        margin: 0px;
    }
}

@media (max-width:768px) {
    .form {
        border-radius: none;
        box-shadow: none;

        &_wrapper {
            .form_input {
                display: flex;
                flex-direction: column;
            }
        }
    }
}