.button {
    text-align: center;

    button {
        border-radius: 30px;
        border: none;

        span {
            padding: 10px 15px;
            font-size: 19px;
            line-height: 24px;
            display: inline-block;
        }
    }

    .activeButton {
        color: #EBEBEB;
        background-color: #171717;
        cursor: pointer;
        box-shadow: 6px 6px 30px 0px rgba(102, 92, 209, 0.4);
    }

    .disabledButton {
        background-color: #cccccc;
        color: #666666;
        cursor: not-allowed;
    }
}

@media (max-width: 570px) {
    .button {
        padding-top: 30px;
        margin: 0 auto;

        button {
            max-width: 250px;
            min-width: 120px;

            span {
                font-size: 16px;
                line-height: 20px;
            }
        }
    }
}

@media (max-width:768px) {
    .button {
        padding-top: 30px;

        button {

            span {
                font-size: 16px;
                line-height: 20px;
            }
        }
    }
}