.dealItem {
    padding-top: 100px;
    text-align: left;
    margin: 0 auto;

    h2 {
        padding-bottom: 50px;
        font-size: 36px;
        line-height: 45px;
        font-weight: bold;
    }

    .dealItem_content {
        display: flex;

        .dealItem_product {

            h3 {
                font-size: 24px;
                font-weight: bold;
                line-height: 30px;
                padding-bottom: 25px;
                margin: 0px;
            }
        }

        .dealItem_form {

            .form {

                &_container {

                    .form_wrapper {
                        display: flex;
                        gap: 100px;
                        padding-left: 100px;

                        .form_input_wrapper {
                            text-align: left;

                            h3 {
                                font-size: 24px;
                                font-weight: bold;
                                line-height: 30px;
                            }

                            .form_input {
                                label {
                                    padding-top: 15px;
                                    font-size: 16px;
                                    line-height: 20px;
                                    margin: 0px;

                                }

                                input {
                                    font-size: 24px;
                                    width: 343px;
                                    border: none;
                                    border-bottom: 2px solid #665CD1;
                                }
                            }
                        }

                        .payment_status_block {
                            display: flex;
                            flex-direction: column;
                            gap: 40px;

                            button {
                                margin-top: 100px;
                            }

                            h3 {
                                text-align: left;
                                font-size: 24px;
                                font-weight: bold;
                                line-height: 30px;

                            }

                            .payment_info {
                                max-width: 300px;
                                border-radius: 5px;
                                background-color: #F1F0FF;
                                text-align: center;
                                font-size: 19px;
                                font-weight: bold;
                                line-height: 24px;
                                padding: 13px 0px 13px 0px;
                            }

                            .status_info {
                                max-width: 300px;
                                border-radius: 5px;
                                text-align: center;
                                font-size: 19px;
                                font-weight: bold;
                                line-height: 24px;
                                padding: 13px 0px 13px 0px;
                                border: 4px solid #F1F0FF;
                                color: #21D929;
                            }

                            .status_info-wait {
                                color: #d92121
                            }
                        }
                    }

                }
            }
        }
    }
}


@media (max-width: 570px) {

    .dealItem {
        padding-top: 70px;


        h2 {
            padding-bottom: 30px;
            font-size: 24px;
            line-height: 30px;
            text-align: center;
        }

        .dealItem_content {
            flex-direction: column;

            .dealItem_product {

                h3 {
                    font-size: 20px;
                    line-height: 25px;
                    padding-bottom: 20px;
                    text-align: left;
                }
            }

            .dealItem_form {

                .form {

                    &_container {

                        .form_wrapper {
                            display: flex;
                            gap: 15px;
                            padding-left: 0px;

                            .form_input_wrapper {
                                h3 {
                                    font-size: 20px;
                                    line-height: 25px;
                                }

                                .form_input {
                                    label {
                                        padding-top: 10px;
                                        font-size: 14px;
                                        line-height: 18px;
                                    }

                                    input {
                                        font-size: 20px;
                                        width: 180px;
                                    }
                                }
                            }

                            .payment_status_block {
                                gap: 24px;

                                button {
                                    margin-top: 15px;

                                    span {
                                        font-size: 12px;
                                        padding: 5px 8px;
                                    }
                                }

                                h3 {
                                    font-size: 20px;
                                    line-height: 25px;
                                }

                                .payment_info {
                                    max-width: 130px;
                                    font-size: 16px;
                                    line-height: 20px;
                                }

                                .status_info {
                                    max-width: 130px;
                                    font-size: 16px;
                                    line-height: 20px;

                                }
                            }
                        }

                    }
                }
            }
        }
    }

}


@media (max-width:768px) {
    .dealItem {

        .dealItem_content {
            flex-direction: column;

            .dealItem_form {

                .form {

                    &_container {

                        .form_wrapper {
                            display: flex;
                            gap: 15px;
                            padding-left: 0px;
                            padding-top: 30px;

                            .payment_status_block {
                                gap: 25px;

                                button {
                                    margin-top: 10px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width:1200px) {

    .dealItem {

        .dealItem_content {
            flex-direction: column;

            .dealItem_form {

                .form {

                    &_container {

                        .form_wrapper {
                            display: flex;
                            gap: 15px;
                            padding-left: 0px;
                            padding-top: 30px;

                            .payment_status_block {
                                gap: 25px;

                                button {
                                    margin-top: 10px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}